
	import Selector from 'vux/src/components/selector/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'
import XAddress from 'vux/src/components/x-address/index.vue'
import Alert from 'vux/src/components/alert/index.vue'

	import cookie from '../../config/cookie' 
	
	export default{
		name: 'datum',
    	components: {
		    Selector,
		    XSwitch,
		    XAddress,
		    Alert
		}, 
		data () {
		    return {
		        isChange:true,//
		        shengshiname:[],//
		        addressData: [],//
		        provinceCode:[],//
		        provinceCodes:'',  //
				province:'',  // 
				cityCode:'', // 
				city:'', //
				areaCode:'',  // 
				area:'',//  
				driverLicense:'', //   
				vehicleSign:'', // 010
				name:'',   //  
				cardNo:'',  // 
				token:'',//token
		        alertShow:false,//
        		alertTxt:'',//
		    }
	    },
	    //
	    created(){
           this.$parent.init();
		},
		//dom
		mounted(){
            var vms=this
            //
            this.token=cookie.Cookie.get("token")?cookie.Cookie.get("token"):'';
            
            vms.$http.post(this.$root.url+"basedata/getAreaInfo", {
    		}, {
		        headers: {'Content-Type': 'application/json;charset=UTF-8',"token":this.token},
		    }).then(function(data) {// 
		        var datas=data.body.data;
		        if(datas&&datas.length>0){
            		vms.addressData=datas
        		}
		    }, function(error) {// 
		        console.log(error)
		    });
		},
	    //
	    methods:{
		    //
			addreChange(va){
				//
				var _this = this;
				this.shengshiname = getaddres(this.provinceCode,this.addressData);
			},
			//
			back(){
				window.history.go(-1)
			},
		    //
		    datumbuts(){
		    	console.log(this.shengshiname);
		    	if(this.provinceCode.length<=0){
		    		this.alertTxt="";
				    this.alertShow=true
				    return false
		    	}else if(this.name==""){
		    		this.alertTxt="";
				    this.alertShow=true
				    return false
		    	}else if(this.cardNo==""){
		    		this.alertTxt="";
				    this.alertShow=true
				    return false
		    	} 
		    	var group = {
		    	 	"province":this.shengshiname.length>0?this.shengshiname[0].name:"",//
		    	 	"provinceCode":this.provinceCode[0],//
					"city":this.shengshiname.length>0?this.shengshiname[1].name:"",//
					"cityCode":this.provinceCode[1], //
					"areaCode":this.areaCode,//
					"area":this.area, //
					"driverLicense":this.isChange==false ? this.driverLicense : '', //
					"vehicleSign":this.Changeval,   //
					"name":this.name,   //
					"cardNo":this.cardNo,//
		    	}
		    	
		    	this.$http.post(this.$root.url+"/apply/applyPolicy",group , {
			        headers: {'Content-Type': 'application/json;charset=UTF-8',"token":this.token},
			    }).then(function(data) {// 
			        if(data.body.ask==0){
			        	this.$router.push({path:'/yesbrand',query: {"Changeval":this.Changeval}})//login
			        }
			    }, function(error) {// 
			        console.log(error)
			    });
		    }
		},
		//
		computed:{
           Changeval(){
           	return this.isChange==true?"1":"0"
           }
        }
	    
	}
	//
	function getaddres(st,arrs){
		var addobj = [];
		for(var i=0;i<arrs.length;i++){
			if(arrs[i].value==st[0]||arrs[i].value==st[1]){
				addobj.push({
					name:arrs[i].name,
					parent:arrs[i].parent,
					id:arrs[i].value
				});
			}
		}
		return addobj;
	}



