
	import XButton from 'vux/src/components/x-button/index.vue'
import Toast from 'vux/src/components/toast/index.vue'

	import cookie from '../../config/cookie' 
    export default {
	  	name: 'login',
	    props: {
	    },
	    components: {
	    	XButton,
	    	Toast
	    },
	    data () {
	      return {
	        phone:'',//
		    verify:'',//
		    toastshow:false,
		    showtext:'!',
		    myreg:/^(((13[0-9]{1})|(15[0-9]{1})|(18[0-9]{1}))+\d{8})$/,   //
	      }
	    },
	    //
	    created(){
			this.$parent.init();
		},
	    //doms
	    mounted:function(){
	      
	    },
	    //
	    methods:{
	    	//
	    	verifybut(){
	    		var _this=this;
            	if(this.phone!==""&&this.myreg.test(this.phone)){
            		this.toastshow=true;
					_this.$http.post(this.$root.url+"member/securityCode", {
		    			"phone": this.phone,
			            "type":2
		    		}, {
				        headers: {'Content-Type': 'application/json;charset=UTF-8'},
//				        emulateJSON: false
				    }).then(function(data) {// 
				
				    }, function(error) {// 
				        console.log(error)
				    });
            	}else{
            		alert("")
            	}
	    	},
	    	//
		  	login2() {
	      	 	var _this=this;
		  		if(this.phone!==""&&this.myreg.test(this.phone)&&this.verify!==""){
					_this.$http.post(this.$root.url+"member/fastLogin", {
		    			"phone": this.phone,
		    			"securityCode": this.verify
		    		}, {
				        headers: {'Content-Type': 'application/json;charset=UTF-8'},
//				        emulateJSON: false
				    }).then(function(data) {// 
				    	console.log(data)
				        var datas=data.body.data;
				        if(data.body.ask==0&&datas){
				        	cookie.Cookie.set("token",datas.token);
				        	this.$router.push({path:'/',query: {}})//
				        }
				
				    }, function(error) {// 
				        console.log(error)
				    });
            	}else{
            		alert("")
            	}
	        }
		    
		},
		//
		computed:{
//         console.log(this.$parent.bbd)
        }
	
	}



