
	import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Toast from 'vux/src/components/toast/index.vue'

	
	export default{
		name: 'forgetpasw',
    	components: {
		    XInput,
		    XButton,
		    Toast
		}, 
		data () {
		    return {
		        idCard:'',//
		        phone:'',//
		        verify:'',//
		        password:'',  //
		        eyeshow:false,
		        toastshow:false,
		        showtext:'!',
		        myreg:/^(((13[0-9]{1})|(15[0-9]{1})|(18[0-9]{1}))+\d{8})$/,   //
		        isIDCard1:/^[1-9]\d{7}((0\d)|(1[0-2]))(([0|1|2]\d)|3[0-1])\d{3}$|^[1-9]\d{5}[1-9]\d{3}((0\d)|(1[0-2]))(([0|1|2]\d)|3[0-1])\d{3}([0-9]|X)$/
		    }
	    },
	    //
	    created(){
			this.$parent.init();
		},
		//dom
		mounted(){
           
		},
	    //
	    methods:{
	    	//
            verifybut(){
            	var _this=this;
            	if(this.phone!==""&&this.myreg.test(this.phone)){
            		this.toastshow=true;
					_this.$http.post(this.$root.url+"member/securityCode", {
		    			"phone": this.phone,
			            "type":3
		    		}, {
				        headers: {'Content-Type': 'application/json;charset=UTF-8'},
//				        emulateJSON: false
				    }).then(function(data) {// 
				
				    }, function(error) {// 
				        console.log(error)
				    });
            	}else{
            		alert("")
            	}
            },
            //
		  	setpasw(){
		  		var _this=this;
		  		if(this.phone!==""&&this.myreg.test(this.phone)&&this.idCard!==""&&this.isIDCard1.test(this.idCard)&&this.password!==""&&this.verify!==""){
					_this.$http.post(this.$root.url+"member/forgetPassword", {
		    			"phone": this.phone,
		    			"idCard": this.idCard,
		    			"password": this.password,
		    			"securityCode": this.verify
		    		}, {
				        headers: {'Content-Type': 'application/json;charset=UTF-8'},
//				        emulateJSON: false
				    }).then(function(data) {// 
				    	console.log(data)
				        var datas=data.body;
				        if(datas.ask==0&&datas.code=="0000"){
				        	this.$router.push({path:'/login',query: {}})//
				        }
				
				    }, function(error) {// 
				        console.log(error)
				    });
            	}else{
            		alert("")
            	}
		  	}
		    
		},
		//
		computed:{
           
        }
	    
	}
	



