
	import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

	import cookie from '../../config/cookie' 
	
	export default{
		name: 'altertel',
    	components: {
		    XInput,
		    XButton
		}, 
		data () {
		    return {
		       verify:'',//
		       phone:'',//
		       myreg:/^(((13[0-9]{1})|(15[0-9]{1})|(18[0-9]{1}))+\d{8})$/,   //
		       token:'',//token
		        
		    }
	    },
	    //
	    created(){
			this.$parent.init();
		},
		//dom
		mounted(){
           this.token=cookie.Cookie.get("token")?cookie.Cookie.get("token"):'';
		},
	    //
	    methods:{
		  	//
	    	verifyBut(){
	    		var _this=this;
            	if(this.phone!==""&&this.myreg.test(this.phone)){
					_this.$http.post(this.$root.url+"member/securityCode", {
		    			"phone": this.phone,
			            "type":4
		    		}, {
				        headers: {'Content-Type': 'application/json;charset=UTF-8'},
//				        emulateJSON: false
				    }).then(function(data) {// 
				
				    }, function(error) {// 
				        console.log(error)
				    });
            	}else{
            		alert("")
            	}
	    	},
	    	//
		  	applybuts(){
		  		var self=this;
		  		if(this.phone!==""&&this.myreg.test(this.phone)&&this.verify!==""){
		  			self.$http.post(this.$root.url+"member/updateUserPhone", {
		    			"phoneNo": this.phone,
			    		"validateCode": this.verify
		    		}, {
				        headers: {'Content-Type': 'application/json;charset=UTF-8',"token":this.token}
				    }).then(function(data) {//    clear
						console.log(data)
			         	var datas=data.body;
				        if(datas.ask==0&&datas.code=="0000"){
				        	cookie.Cookie.set("token",datas.data.token);
				        	this.$router.push({path:'/personal',query: {}})//login
				        }
				    }, function(error) {// 
				        console.log(error)
				    });
			        
            	}else{
            		alert("")
            	}
		  	}
		    
		},
		//
		computed:{
           
        }
	    
	}
	



