
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'
import Cell from 'vux/src/components/cell/index.vue'

import cookie from '../../config/cookie' 
/**/
export default {
  	name: 'personal',
    components: {
	    Tabbar,
	    TabbarItem,
	    Cell
  	},
    data () {
	    return {
	       url:"",
	       token:'',//token
	       memberName:'',//
	       orderNuber:'',//
	       myQcodeUrl:''
	    }
	},
	//
    created(){
       this.$parent.init();
	},
	//dom
	mounted(){
		this.token=cookie.Cookie.get("token")?cookie.Cookie.get("token"):'';
		
		this.$http.post(this.$root.url+"member/getMemberInfo", {
		}, {
	        headers: {'Content-Type': 'application/json;charset=UTF-8',"token":this.token},
	    }).then(function(data) {// 
	        var datas=data.body.data;
	        console.log(data)
	        if(data.body.ask==0&&datas&&datas.memberInfo){
        		this.memberName = datas.memberInfo.memberName;
        		this.myQcodeUrl = '/myQcode?phone='+datas.memberInfo.phoneNo;
        		
        		console.log(this.phone)
    		}

	    }, function(error) {// 
	        console.log(error)
	    });
	    //
	    this.$http.post(this.$root.url+"order/count", {
	    	"orderStatus":0
		}, {
	        headers: {'Content-Type': 'application/json;charset=UTF-8',"token":this.token},
	    }).then(function(data) {// 
	        var datas=data.body.data;
	        console.log(data)
	        if(data.body.ask==0&&datas){
        		this.orderNuber = datas;
    		}
	    }, function(error) {// 
	        console.log(error)
	    });
	    
	},
	//
    methods:{
	  	presolink(){
            this.$router.push({path:'/message',query: {}})//login
	  	}
	}
  
}




